.study__title {
  margin-top: 77px;
  margin-bottom: 38px;
  font-family: RobotoLight, Arial, sans-serif;
  font-weight: 400;
  font-size: 2.25em;
  color: #333;
  text-align: center; }
  .study__title--white {
    color: #fff; }

.study__text {
  margin: 0 0 6px 0;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 1.125em;
  line-height: 1.33em;
  color: #333;
  text-align: center; }
  .study__text--white {
    color: #fff; }

.study-service__wrap {
  margin-top: 77px;
  margin-bottom: 212px; }

.study-service__center-pic-wrap {
  height: 100%;
  vertical-align: center; }
  .study-service__center-pic-wrap img {
    width: 100%;
    height: auto; }

.study-service__col--left {
  text-align: right; }
  @media (max-width: 768px) {
    .study-service__col--left {
      text-align: left; } }

.study-service__col--right {
  text-align: left; }

.study-item {
  margin: 0 -15px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 20px; }
  .study-item--highlight {
    background-color: #a3d3e5; }
  .study-item__title {
    font-family: RobotoLight, Arial, sans-serif;
    font-weight: 400;
    font-size: 1.5em; }
  .study-item__text {
    font-family: Arial, sans-serif;
    font-weight: 400;
    font-size: 1em;
    line-height: 1.5em; }

.study-access {
  width: 100%;
  margin-bottom: 140px;
  padding-top: 110px;
  padding-bottom: 142px;
  background-image: url("../img/foto_demo.jpg");
  background-position: center center;
  background-size: cover; }
  .study-access .study__title {
    margin-bottom: 20px; }
  .study-access__col-wrap {
    margin-top: 120px; }
  .study-access__form {
    margin-top: 67px; }
  .study-access__form-input {
    width: 100%;
    padding: 6px 20px;
    margin-bottom: 15px;
    text-align: center; }
  .study-access__button-submit {
    margin-top: 37px;
    padding-left: 50px;
    padding-right: 50px; }

.study-process {
  margin-bottom: 200px;
  width: 100%; }
  .study-process__col-wrap {
    width: 100%;
    background-image: url("../img/study3.png");
    background-position: 45vw 0%;
    background-size: contain;
    background-repeat: no-repeat; }
    @media (max-width: 992px) {
      .study-process__col-wrap {
        background-image: none; } }
  .study-process .study__title {
    margin-bottom: 20px; }

.study-details {
  margin-bottom: 160px;
  padding-top: 100px;
  padding-bottom: 120px;
