    padding-right: 100px; }

.study-cashback__group {
  margin-top: 75px; }

.study-cashback__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-bottom: 50px; }

.study-cashback__item-num {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  margin-bottom: 18px;
  border-radius: 32px;
  background-color: #1422aa;
  font-size: 2.25em;
  font-weight: 600;
  color: #fff; }

.study-cashback__item-text {
  text-align: center;
  font-size: 1em;
  line-height: 1.2em;
  color: #333; }

.study-cashback__item-ellipse {
  position: absolute;
  width: 125px;
  height: 40px;
  top: 32px;
  right: calc(-50% + 57px);
  background-image: url(../img/ellipse.svg); }
  .study-cashback__item-ellipse--rotated {
    top: 0;
    background-image: url(../img/ellipse_rotated.svg); }

.study-cashback__subtitle {
  margin-bottom: 50px;
  font-family: RobotoLight, Arial, sans-serif;
  font-weight: 400;
  font-size: 3em;
  line-height: 1.4em;
  color: #333; }
  .study-cashback__subtitle span {
    display: block;
    font-family: RobotoBold, Arial, sans-serif;
    font-weight: 700;
    color: #1422aa; }
    .study-cashback__subtitle span:after {
      content: '₽';
      display: inline-block;
      margin-left: 0.25em;
      font-size: 0.75em; }

.study-cashback__details-wrap {
  margin-top: 200px; }

.study-cashback__details-group {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap; }

.study-cashback__detail {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  min-width: 33%;
  padding-right: 20px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  min-height: 120px; }
  .study-cashback__detail-sum {
    font-size: 3em;
    font-weight: 600;
    color: #999999; }
    .study-cashback__detail-sum span {
      display: inline-flex;
      align-items: baseline;
      font-family: serif;
      font-size: 1.75em;
      line-height: 16px; }
    .study-cashback__detail-sum::before {
      content: 'Кэшбек';
      display: block;
      font-size: 14px;
      color: #a3d3e5; }
    .study-cashback__detail-sum::after {
      content: '₽';
      display: inline-block;
      margin-left: 0.25em;
      font-size: 24px; }
  .study-cashback__detail-text {
    color: #333; }
  .study-cashback__detail-img {
    height: 160px;
    width: 100%;
    transform: translateY(-30px); }
    .study-cashback__detail-img img {
      height: 100%;
      max-height: 160px;
      width: auto; }

.study-offer {
  margin-top: 150px;
  margin-bottom: 150px; }
  .study-offer__wrap {
    margin-top: 75px; }
  .study-offer__text {
    margin-bottom: 10px;
    font-size: 1em;
    color: #333; }
  .study-offer__button {
    margin-left: 20px;
    margin-bottom: 30px; }
  .study-offer__form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
  .study-offer__form-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    position: relative;
    width: 100%;
    max-width: 480px;
    min-width: 320px;
    height: 54px;
    margin-right: 30px;
    padding-left: 65px;
    margin-bottom: 20px;
    border-radius: 100px;
    border: 1px solid #bcbcbc; }
  .study-offer__input-ico {
    position: absolute;
    top: 12px;
    left: 22px;
    color: #1422aa; }
  .study-offer__input-info {
    display: inline-block;
    flex-grow: 7;
    padding-right: 10px;
    font-size: 1em;
    font-weight: 400;
    color: #bcbcbc;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipse; }
  .study-offer__input-file {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1; }
  .study-offer__input-file-btn {
    display: inline-block;
    flex-grow: 1;
    margin: 0;
    padding: 12px 32px;
    border-radius: 100px;
    background-color: #f6f6f6;
    text-align: center;
    font-size: 1em;
    font-weight: 400;
    color: #bcbcbc;
    border: 1px solid #bcbcbc;
    cursor: pointer; }
    .study-offer__input-file-btn:hover {
      background-color: #1E33FF;
      color: #fff; }
    .study-offer__input-file-btn:focus {
      outline: 1px dotted #000;
      outline: -webkit-focus-ring-color auto 5px; }
  .study-offer__form-button {
    background-color: #fecf73;
    font-size: 1em;
    color: #000; }
